<template>
  <div style="padding-top: 51px;">
    <div>示例演示</div>
    <pr-row>
      <pr-col span="12">
        <div class="pr-form-group">
          <div class="pr-form-item">
            <span class="pr-form-item-label">
              <i class="pr-required"></i>
              用户名：
            </span>
            <span class="pr-form-item-input">
              <pr-input
                v-model="formData.username"
                type="text"
                placeholder="请输入2-10字符"
                width="300"
              />
            </span>
          </div>

          <div class="pr-form-item">
            <span class="pr-form-item-label">
              <i class="pr-required"></i>
              邮箱：
            </span>
            <span class="pr-form-item-input">
              <pr-input
                v-model="formData.email"
                type="text"
                placeholder="请输入常用邮箱地址"
                width="300"
              />
            </span>
          </div>

          <div class="pr-form-item">
            <span class="pr-form-item-label">
              <i class="pr-required"></i>
              手机号：
            </span>
            <span class="pr-form-item-input">
              <pr-input
                v-model="formData.mobile"
                type="text"
                placeholder="请输入手机号"
                width="300"
              />
            </span>
          </div>

          <div class="pr-form-item">
            <span class="pr-form-item-label"> </span>
            <span class="pr-form-item-input">
              <pr-button
                type="primary"
                @click="handleSave"
                :loading="summiting"
              >
                保存
              </pr-button>
            </span>
          </div>
        </div>
      </pr-col>
      <pr-col span="12">
        <Example />
      </pr-col>
    </pr-row>
  </div>
</template>

<script>
import Example from "../docs/validator/index/example.md";
export default {
  components: {
    Example,
  },
  data() {
    return {
      formData: {
        username: "",
        email: "",
      },
      summiting: false,
    };
  },
  methods: {
    handleSave() {},
  },
};
</script>

<style lang="less">
.pr-col-12 {
  max-height: calc(100vh - 91px);
  overflow: auto;
}
.pr-form-group {
  margin: 20px auto;
}

.pr-form-item-label {
  width: 120px;
}

.demo-container {
  height: auto;
}
.docs-trans {
  display: none;
}
</style>



